import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";//码值
import { Message } from 'primeng/primeng';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { ProgressSpinnerModule } from 'primeng/progressSpinner'
import { Router } from '@angular/router';
import { TZB_CUS_DOWNLOAD } from '../../../../../../@uisftech/common/service/http.constant';
@Component({
  selector: 'credit-trail-preview',
  templateUrl: './credit-trail-preview.component.html',
  styleUrls: ['./credit-trail-preview.component.scss']
})
export class CreditTrailPreview implements OnInit {
  datas: any=[];
  trailData: any;
  sales: any;
  msgs: Message[] = [];//提示信息
  blocked: boolean = false;
  DOWNLOAD: string = TZB_CUS_DOWNLOAD;
  pTooltip1: string = '最终还款日=账单日+还款宽限期';
  pTooltip2: string = '费用合计=取现手续费+分期手续费+年费+其他费用';
  pTooltip3: string = '分期应收利息=(上期分期应计利息+本期分期应计利息)*全额还款';
  pTooltip4: string = '消费应收利息=(上期消费应计利息+本期消费应计利息)*全额还款';
  pTooltip5: string = '取现应收利息=(上期正数取现金额+本期正数取现金额)*利率*天数';
  pTooltip6: string = '应收复利=(上期应计复利+本期应计复利)*全额还款*计收复利';
  constructor(
    public commfunc: Commfunc,
    public router: Router,
    private creditCcmService: CreditCcmService, ) {

  }
  ngOnInit() {
    this.queryData();
    this.trailData = this.commfunc.getSessionDataCH("creditTrailData");
    this.sales = [{}];
  }
  // 数据查询
  queryData() {
    let previewData = JSON.parse(this.commfunc.getSessionDataCH("creditPreviewData"))
    const params = {
      cardNbr: previewData.cardNbr,
      startBillDate: previewData.startBillDate,
      endBillDate: previewData.endBillDate
    }
    this.blocked = true;
    this.creditCcmService.queryCreditCardIntTrailShrink(params).subscribe(data => {
      if (data.returnCode.code === 'success') {
        this.blocked = false;
        this.datas = data.list;
      } else {
        this.blocked = false;
        this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
      }
    }, err => {
      // this.router.navigate(['pages/tzb/custom/credit-ccm/credit-trail/trail-detail']);
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
    });
  }
  // 确认，下载
  confirm() {
    let previewData = JSON.parse(this.commfunc.getSessionDataCH("creditPreviewData"))
    const params = {
      cardNbr: previewData.cardNbr,
      startBillDate: this.commfunc.transDateN(previewData.startBillDate) || null,
      endBillDate: this.commfunc.transDateN(previewData.endBillDate) || null,
    };
    this.creditCcmService.loadCreditCardTrialShrinkExcel(params).subscribe(data => {
      if (data.returnCode.code === 'success') {
        let datas = data.list;
        let fileUrl=data.fileUrl;
        let fileName=data.fileName;
        window.open(this.DOWNLOAD + "?fileName=" + encodeURI(fileName) + "&fileUrl=" + fileUrl);
        this.commfunc.setSessionDataCH("creditTrailData", JSON.stringify(datas))
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
      });
  }
  //返回
  retunLevel() {
    window.history.back();
  }
}
