import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";//码值
import { Router, ActivatedRoute } from '@angular/router';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';//日期格式化;
import { DecimalPipe } from '@angular/common';
import * as moment from 'moment';//日期插件
import { MessageService } from 'primeng/components/common/messageservice';
import { Message } from 'primeng/primeng';
import { TrailQuery } from "./credit-trail-query.bean";
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
  selector: 'credit-trail-query',
  templateUrl: './credit-trail-query.component.html',
  styleUrls: ['./credit-trail-query.component.scss'],
  providers: [MessageService, DecimalPipe]
})
export class CreditTrailQuery implements OnInit {
  // @Output("givData")
  // givData = new EventEmitter();
  // @Input("inValue")
  // inData: any;

  // dutyAffirmLevel: any = [];//等级码值
  msgs: Message[] = [];//提示信息
  ch: any = CALENDAR_CN;//日期格式化
  queryCriteria: any = new TrailQuery();
  datas :any;
  constructor(
    public commfunc: Commfunc,
    public decimalPipe: DecimalPipe,
    private creditCcmService: CreditCcmService,
    public router: Router,
  ) {
  }

  ngOnInit() {
  }
  // 查询
  query() { 
    const params = {
      cardNbr: this.queryCriteria.cardNbr,
      startBillDate: this.commfunc.transDateN(this.queryCriteria.startBillDate) || null,
      endBillDate: this.commfunc.transDateN(this.queryCriteria.endBillDate) || null
    };
    if (this.queryCriteria.cardNbr == null || this.queryCriteria.startBillDate == null || this.queryCriteria.endBillDate == null) {
      this.msgs = [];
      this.msgs.push({ severity: "info", summary: '提示', detail: '请输入信用卡卡号、账期！' })
    }else{
    this.creditCcmService.queryCreditCardTrailProcessService(params).subscribe(data => {
      if (data.returnCode.code === 'success') {
        let datas = data.list;
        this.datas = data.list;
        this.commfunc.setSessionDataCH("creditTrailData", JSON.stringify(datas))
        this.commfunc.setSessionDataCH("creditPreviewData", JSON.stringify(params))
      } else {
        this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
      }
    }, err => {
      // this.router.navigate(['pages/tzb/custom/credit-ccm/credit-trail/trail-detail']);
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
    });
    }
  }
  reset() {
    this.queryCriteria.cardNbr = null;
    this.queryCriteria.startBillDate = null;
    this.queryCriteria.endBillDate = null;
  }


}
