import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';

import { CreditTrail } from "./credit-trail.component";
import { CreditTrailQuery } from "./credit-trail-query/credit-trail-query.component";
import { CreditTrailDetail } from "./credit-trail-detail/credit-trail-detail.component";
import { CreditTrailPreview } from "./credit-trail-preview/credit-trail-preview.component";

export const routes: Routes = [
    {
        path: '',
        component: CreditTrail,
        children: [
            { path: 'trail-query', component: CreditTrailQuery },
            { path: 'trail-detail', component: CreditTrailDetail },
            { path: 'trail-preview', component: CreditTrailPreview },
        ]
    }
]

export const CreditTrailRouting: ModuleWithProviders = RouterModule.forChild(routes);